:root {
  --half-gap: 10px;
  --gap: calc(2 * var(--half-gap));
  --col1: calc(100% / 12 - var(--gap));
  --cg: calc(var(--gap) + var(--col1));
  --col2: calc(var(--col1) + var(--cg));
  --col3: calc(var(--col2) + var(--cg));
  --col4: calc(var(--col3) + var(--cg));
  --col5: calc(var(--col4) + var(--cg));
  --col6: calc(var(--col5) + var(--cg));
  --col7: calc(var(--col6) + var(--cg));
  --col8: calc(var(--col7) + var(--cg));
  --col9: calc(var(--col8) + var(--cg));
  --col10: calc(var(--col9) + var(--cg));
  --col11: calc(var(--col10) + var(--cg));
  --col12: calc(var(--col11) + var(--cg));

  --ml0: var(--half-gap);
  --ml1: calc(var(--col1) + 3 * var(--half-gap));
  --ml2: calc(var(--ml1) + var(--cg));
  --ml3: calc(var(--ml2) + var(--cg));
  --ml4: calc(var(--ml3) + var(--cg));
  --ml5: calc(var(--ml4) + var(--cg));
  --ml6: calc(var(--ml5) + var(--cg));
  --ml7: calc(var(--ml6) + var(--cg));
  --ml8: calc(var(--ml7) + var(--cg));
  --ml9: calc(var(--ml8) + var(--cg));
  --ml10: calc(var(--ml9) + var(--cg));
  --ml11: calc(var(--ml10) + var(--cg));

  --container-padding: 40px;
}

.container,
.container-fluid {
  display: inline-block;
  width: 100%;
  padding: var(--container-padding) 10px;
}
.container > .row,
.container-fluid > .row {
  display: inline-block;
  width: 100%;
  margin: 30px 0;
}
.row > [class*='col-'] {
  float: left;
  margin-left: var(--half-gap);
  margin-right: var(--half-gap);
}
.row > .reverse {
  float: right;
}
.row > .forward {
  float: left;
}
.row > .col-1 {
  width: var(--col1);
}
.row > .col-2 {
  width: var(--col2);
}
